.correctionalHealthSection {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 3rem 0;
}

.correctionalHealthSectionImage {
    width: 100%;
    overflow: hidden;
}

.correctionalHealthSectionImage img {
    width: 100%;
    height: auto;
    border-radius: 8px;
    object-fit: cover;
    display: block;
}

.correctionalHealthSectionText {
    text-align: center;
    width: 100%;
    padding: 1rem;
}

.correctionalHealthSectionText h1 {
    font-size: 1.5rem;
    color: rgba(23, 102, 121, 0.877);
    font-family: 'El Messiri', sans-serif;
    text-align: left;
    font-weight: normal;
}

.correctionalHealthSectionText h2 {
    color: rgb(4, 66, 117);
    font-weight: normal;
    font-size: 1.2rem;
    font-family: 'Roboto', sans-serif;
    text-align: left;
}

.correctionalHealthSectionText p {
    font-size: 15px;
    color: #666;
    line-height: 1.5;
    margin-bottom: 20px;
    text-align: left;
    padding: .5rem 0;
}

.correctionalHealthSectionText ul li {
    text-align: left;
    list-style-type: disc;
}

.correctionalHealthSectionText ul {
    padding: 0 1rem ;
}

.correctionalHealthSectionBtn {
    text-align: left;
}

.correctionalHealthSectionBtn button {
    background-color: rgb(31, 133, 192); /* Change color as needed */
    padding: .8rem 1.5rem;
    background-color: (31, 133, 192); /* Change color as needed */
    color: #ffffff;
}

/* Responsive layout for larger screens */
@media screen and (max-width: 940px) {
    .correctionalHealthSection {
        flex-wrap: wrap;
    }

    .correctionalHealthSectionBtn {
        text-align: center;
    }

    .correctionalHealthSectionText p {        
        text-align: justify;
    }

    .correctionalHealthSectionText h2 {
        text-align: center;
    }

    .correctionalHealthSectionText h1 {
        text-align: center;
    }


}